import React from 'react';
import ReactDOM from 'react-dom';

import { Provider } from 'react-redux';
import { createStore } from 'redux';

import reducers from './redux/init_redux';


class Food extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            ready: false,
            store: null
        }
    }

    componentDidMount() {
        createStore(reducers, /* some ajaxy thing where we get the server store */);
    }

    render() {
        return (
            <div>
                <h1>Food is {this.props.name}</h1>
                <h2>Production? {process.env.NODE_ENV}</h2>
            </div>
        )
    }
}

const mountNode = document.getElementById('app');
ReactDOM.render(<Food name="Food" />, mountNode);